
input[id^=id]
{
    display: none;
}

label
{
    position: relative;
    width: 25px;
    height: 25px;
    background-color: var(--primary-background);
    -webkit-transform: translateY(30%);
        -ms-transform: translateY(30%);
            transform: translateY(30%);
    border-radius: 50%;
    cursor: pointer;
    -webkit-transition: 0.2s ease transform, 0.2s ease background-color, 0.2s ease box-shadow;
    -o-transition: 0.2s ease transform, 0.2s ease background-color, 0.2s ease box-shadow;
    transition: 0.2s ease transform, 0.2s ease background-color, 0.2s ease box-shadow;
    overflow: hidden;
    z-index: 1;
}

label:before
{
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    width: 70%;
    height: 70%;
    margin: 0 auto;
    background-color: var(--secondory-background);
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    border-radius: 50%;
    -webkit-transition: 0.2s ease width, 0.2s ease height;
    -o-transition: 0.2s ease width, 0.2s ease height;
    transition: 0.2s ease width, 0.2s ease height;
}

label:hover:before
{
    width: 55%;
    height: 55%;
}

#tick_mark
{
    position: absolute;
    right: 0;
    left: 0;
    width: 60%;
    height: 60%;
    margin-left: 12%;
    margin-top: 5%;
    -webkit-transform: rotateZ(-40deg);
        -ms-transform: rotate(-40deg);
            transform: rotateZ(-40deg);
}

#tick_mark:before, #tick_mark:after
{
    content: "";
    position: absolute;
    background-color: #fff;
    opacity: 0;
    -webkit-transition: 0.2s ease transform, 0.2s ease opacity;
    -o-transition: 0.2s ease transform, 0.2s ease opacity;
    transition: 0.2s ease transform, 0.2s ease opacity;
}

#tick_mark:before
{
    left: 0;
    bottom: -1px;
    width: 4px;
    height: 10px;
}

#tick_mark:after
{
    left: 1px;
    bottom: -1px;
    width: 14px;
    height: 4px;
}

input[id^=id]:checked + label
{
    background-color: #07d410;
}

input[id^=id]:checked + label:before
{
    width: 0;
    height: 0;
}

input[id^=id]:checked + label #tick_mark:before, input[id^=id]:checked + label #tick_mark:after
{
    -webkit-transform: translate(0);
        -ms-transform: translate(0);
            transform: translate(0);
    opacity: 1;
}


